


<template>
  <div class="comment-counter">
      <el-container>
        <el-main>
            <h1>HAILKIRA</h1>
            <el-row :gutter="20">
                <el-col :span="12">
                    <el-input v-model="vidId" placeholder="youtube video Id" type="text">
                        <template slot="prepend">Video Id</template>
                    </el-input>
                </el-col>
                <el-col :span="6">
                    <el-input v-model="bonusText" placeholder="bonus text" type="text">
                        <template slot="prepend">Bonus Text</template>
                    </el-input>
                </el-col>
                <el-col :span="6">
                    <el-input v-model="bonusAmount" placeholder="bonus amount" type="number">
                        <template slot="prepend">Bonus Amount</template>
                    </el-input>
                </el-col>
            </el-row>
            <br />
            <br />
            <el-button @click="scoreComments">Score Comments</el-button>
            <br />
            <el-table
            :data="resultArray"
            >
                <el-table-column
                    prop="displayName"
                    label="Display Name">
                </el-table-column>
                <el-table-column
                    prop="points"
                    label="Points">
                </el-table-column>
                <el-table-column
                    prop="comments"
                    label="Comments"
                    :formatter="formatComments"
                >
                </el-table-column>
                <el-table-column
                    prop="videoId"
                    label="Video Id">
                </el-table-column>
            </el-table>
        </el-main>
      </el-container>
  </div>
</template>

<script>
export default {
  name: 'CommentCounter',
  props: {
  },
  data: function(){
    return {
        vidId:'IFCSBwv7M_E',
        base:'https://www.googleapis.com/youtube/v3/commentThreads?',
        key:'key=AIzaSyAnOLXNO4MiB1H9yu8AVkHTnPdkhjY7Epc',
        options:'&textFormat=plainText&part=snippet&maxResults=2',
        vid:'&videoId=',
        results:{},
        resultArray:[],
        bonusText:'',
        bonusAmount:10,
        loading:false
    }
  },
  methods: {
      formatComments(row,info,comments){
          return comments.join(' | ');
      },
      buildResultArray(){
          this.resultArray = Object.keys(this.results).map(key=>{
              return this.results[key];
          });
          //this.vidId = '';
          //this.bonusText = '';
          this.loading = false;
      },
      scoreComments(){
          if(!this.vidId){
              alert('Video id required');
              return false;
          }
          this.getComments(this.base+this.key+this.options+this.vid+this.vidId);

          // &textFormat=plainText&part=snippet&videoId=IFCSBwv7M_E&maxResults=50
          //part=snippet,replies
      },
      getComments(url){
        var vueThis = this;
        var xhttp = new XMLHttpRequest();
        xhttp.onreadystatechange = function() {
            if (this.readyState == 4 && this.status == 200) {
                let data = JSON.parse(this.responseText);
                if(!data.items){
                    alert('err, try again or contact support');
                    return false;
                }
                vueThis.results = data.items.reduce((resObj,item)=>{
                    let uid = item.snippet.topLevelComment.snippet.authorChannelId.value+'_vid_'+vueThis.vidId;
                    if(!resObj[uid]){
                        resObj[uid] = {
                            displayName:item.snippet.topLevelComment.snippet.authorDisplayName,
                            comments:[],
                            videoId:vueThis.vidId,
                            points:100
                        }
                    }
                    vueThis.checkForBonus(resObj[uid],item.snippet.topLevelComment.snippet.textDisplay);
                    if(resObj[uid].comments.indexOf(item.snippet.topLevelComment.snippet.textDisplay) === -1){
                        resObj[uid].comments.push(item.snippet.topLevelComment.snippet.textDisplay);
                    }
                    return resObj;
                },vueThis.results);
                if(data.nextPageToken){
                    vueThis.getComments(vueThis.base+vueThis.key+vueThis.options+vueThis.vid+vueThis.vidId+'&pageToken='+data.nextPageToken);
                }
                else{
                    vueThis.buildResultArray();
                }
            }
        };
        xhttp.open("GET", url, true);
        xhttp.send();
    },
    checkForBonus(obj,comment){
        if(this.bonusText && this.bonusAmount > 0 && !obj.bonus){
            if(comment.toLowerCase().indexOf(this.bonusText.toLowerCase()) >= 0){
                obj.bonus = true;
                obj.points += parseFloat(this.bonusAmount);
            }
        }
    }
  }
}
</script>

